
.me-view {
  padding: 5px 0 10px;
  overflow-y: scroll;
}
.fb-cell {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 5px;
  padding: 15px;
  margin: 5px 5px 0 5px;
}
.fb-cell.error {
    border: 1px solid #ff5200;
}
.fb-cell .fb-cell-icon {
    width: 24px;
    height: 24px;
    margin-right: 8px;
}
.fb-cell .fb-cell-input {
    background-color: transparent;
    width: 100%;
    border: 0;
    font-size: 16px;
    margin: 4px 6px;
}
.fb-cell .fb-cell-input::-webkit-input-placeholder, .fb-cell .fb-cell-input::input-placeholder, .fb-cell .fb-cell-input:-webkit-input-placeholder, .fb-cell .fb-cell-input:-input-placeholder {
      color: #fff;
}
.fb-cell .fb-cell-input::-webkit-input-placeholder {
    color: #fff;
}
.fb-cell .fb-cell-tab {
    color: #fff;
}



