
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#24aac1+0,4abae2+100 */
  background: #24aac1;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #24aac1 0%, #4abae2 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#24aac1', endColorstr='#4abae2',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
body, html {
  width: 100%;
  height: 100%;
}
#app {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  box-orient: vertical;
  -ms-flex-direction: column;
      flex-direction: column;
}
.router-view {
  /*background-color: rgba(255, 255, 255, 0.8);*/
  transition: background-color .4s;
  -webkit-box-flex: 1;
  -webkit-box: 1;
  -ms-flex: 1;
      flex: 1;
}
.spring {
  -webkit-box-flex: 1;
  -webkit-box: 1;
  -ms-flex: 1;
      flex: 1;
}
button, input, textarea {
  background-color: transparent;
  border: 0;
  display: block;
  resize: none;
}
button:active, button:focus, button:hover, input:active, input:focus, input:hover, textarea:active, textarea:focus, textarea:hover {
    outline: none;
}



