
@charset "UTF-8";
.fb-bubble {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.fb-bubble .mask {
    position: absolute;
    width: 100%;
    height: 100%;
}
.fb-bubble .fb-bubble-main {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.92);
    color: #000000;
    border-radius: 8px;
    padding: 5px 0;
    min-width: 120px;
    text-align: center;
}
.fb-bubble .fb-bubble-main > * {
      list-style: none;
      line-height: 40px;
      border-bottom: 1px solid #e7e7e7;
      padding: 0, 10px;
      -ms-user-select: none;
      -moz-user-select: none;
      -webkit-user-select: none;
      user-select: none;
      font-size: 16px;
      font-weight: 200;
}
.fb-bubble .fb-bubble-main > *:active {
        background-color: rgba(0, 0, 0, 0.1);
}
.fb-bubble .fb-bubble-main > *:last-of-type {
        border-bottom: 0;
}
.fb-bubble .fb-bubble-main.up {
      transform: translate(50%, -100%);
      margin-bottom: 18px;
}
.fb-bubble .fb-bubble-main.up:after {
        content: '';
        display: block;
        border-color: rgba(255, 255, 255, 0.92) transparent transparent transparent;
        border-style: solid solid solid solid;
        border-width: 10px;
        /* 設定 width、height 可更好理解原理 */
        height: 0px;
        width: 0px;
        margin-bottom: -25px;
        margin-left: 50%;
        transform: translate(-50%, 0);
}
.fb-bubble .fb-bubble-main.down {
      transform: translate(50%, 0);
      margin-top: 18px;
}
.fb-bubble .fb-bubble-main.down:before {
        content: '';
        display: block;
        border-color: transparent transparent rgba(255, 255, 255, 0.92) transparent;
        border-style: solid solid solid solid;
        border-width: 10px;
        /* 設定 width、height 可更好理解原理 */
        height: 0px;
        width: 0px;
        margin-top: -25px;
        margin-left: 50%;
        transform: translate(-50%, 0);
}
.fb-bubble .fb-bubble-main.left {
      transform: translate(-100%, -50%);
      margin-right: 18px;
}
.fb-bubble .fb-bubble-main.right {
      transform: translate(100%, -50%);
      margin-left: 18px;
}



