
.alert-pro {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.2);
}
.alert-pro .alert-main {
    width: 80%;
    background-color: rgba(255, 255, 255, 0.94);
    color: #1e5e73;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -40%;
    margin-top: -20%;
    border-radius: 8px;
}
.alert-pro .alert-main .alert-desc {
      padding: 20px;
}
.alert-pro .alert-main .alert-btn {
      height: 50px;
      line-height: 50px;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}



